<template>
  <div class="my">
    <div class="my-box" :style="{backgroundImage: 'url(' + userInfo.userBg + ')'}">
      <div class="change-bg clearfix">
        <div class="fr cbg" v-if="joinLogin">
          <div>更换背景</div>
          <van-uploader class="upload-box" :max-size="maxSize" :after-read="uploadFile" />
        </div>
      </div>
      <div class="user-info">
        <div v-if="joinLogin">
          <div class="clearfix">
            <div class="my-img fl">
              <img class="auto-img" :src="userInfo.userImg" alt />
            </div>
            <div class="nickname fl">{{userInfo.nickName}}</div>
            <div class="text fl">简介：{{userInfo.desc == '' ? '这家伙很懒，什么都没留下' : userInfo.desc}}</div>
          </div>
        </div>
        <div class="login-text" v-else @click="goPage('Login')">
          <i class="fa fa-user-circle fa-2x" aria-hidden="true"></i>
          <span>登录</span>
          
        </div>
      </div>
    </div>

    <div>
      <van-cell-group>
        <van-cell
          @click="goPage(item.routeName)"
          :title="item.title"
          is-link
          v-for="(item, index) in litsData"
          :key="index"
        >
          <template #icon>
            <i :class="item.icon" aria-hidden="true"></i>
          </template>
        </van-cell>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
import "../../assets/css/my.less";
export default {
  data() {
    return {
      //上传文件1MB
      maxSize: 1 * 1024 * 1024,
      litsData: [
        {
          title: "账号管理",
          routeName: "Account",
          icon: "fa fa-user-circle-o"
        },
        {
          title: "地址管理",
          routeName: "MyAddress",
          icon: "fa fa-map-marker"
        },
        {
          title: "我的收藏",
          routeName: "Like",
          icon: "fa fa-heart-o"
        },
        {
          title: "安全中心",
          routeName: "Secure",
          icon: "fa fa-shield"
        }
      ],

      //用户信息
      userInfo: {},

      joinLogin: false
    };
  },

  created() {
    this.getUserInfo();
    //判断是否登录
    if (localStorage.getItem("__Tk")) {
      this.joinLogin = true;
    }
  },

  methods: {
    //跳转页面
    goPage(name) {
      if (name === "Login") {
        this.$router.push({ name });
      } else {
        if (!this.joinLogin) {
          this.$toast("请先登录");
        } else {
          this.$router.push({ name });
        }
      }
    },

    //获取用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          

          if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //获取上传文件的base64
    uploadFile(file) {
      

      //获取图片base64
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );

      //获取图片类型
      let imgType = file.file.type.split("/")[1];

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>
